.stopwatch {
  background-color: none;
  border: 1vmin dotted #000;
  border-radius: 50%;
  justify-content: center;
  align-items: center;
  width: 80vmin;
  height: 80vmin;
  display: flex;
  position: relative;
}

.stopwatch__min-hand {
  transform-origin: 50% 100%;
  background-color: #0700ff;
  border-radius: 0;
  width: 3vmin;
  height: 20vmin;
  animation: 3600s steps(60, end) infinite rotate;
  position: absolute;
  top: 20vmin;
}

.stopwatch__sec-hand {
  transform-origin: 50% 100%;
  background-color: #2c8000;
  border-radius: 0;
  width: 1.5vmin;
  height: 38vmin;
  animation: 60s linear infinite rotate;
  position: absolute;
  top: 2vmin;
}

.stopwatch__center {
  z-index: 1;
  background-color: #f6a603;
  border-radius: 50%;
  width: 5vmin;
  height: 5vmin;
  position: absolute;
}

.stopwatch--speed-up .stopwatch__min-hand {
  animation: 600s steps(60, end) infinite rotate;
}

.stopwatch--speed-up .stopwatch__sec-hand {
  animation: 10s linear infinite rotate;
}

@keyframes rotate {
  from {
    transform: rotateZ(0);
  }

  to {
    transform: rotateZ(360deg);
  }
}

* {
  margin: 0;
}

body {
  justify-content: center;
  align-items: center;
  height: 100vh;
  display: flex;
}

